<template>
  <div class="InviteList fb-col">
    <div class="fb-row" v-for="(item, index) in onOffFilter(data,filters)" :key="index">
      <h4 class="number">{{ parseInt(index)+1 }}. </h4><div v-for="(str, i) in item" :key="i">{{str}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'InviteList',
  data () {
    return {
      exceptionArr: ['Surname', 'Name', 'Жених', 'Невеста']
    }
  },
  props: {
    data: Object,
    filters: Boolean
  },
  methods: {
    onOffFilter (data, onOff) {
      if (onOff) {
        return this.filter(data, 2, this.exceptionArr)
      } else return data
    },
    filter (data, numCol, exceptionArr) {
      var numColDefault = (numCol === undefined)
      var boolDelete = false
      var filterData = {}
      var tempmas = []
      for (var row in data) {
        tempmas = []
        numCol = numColDefault ? data[row].length : numCol
        for (var i = 0; i < numCol; i++) {
          if (this.myRegExp(exceptionArr, data[row][i] || '')) {
            tempmas.push(data[row][i] || '')
          } else boolDelete = true
        }
        filterData[row] = tempmas
        if (boolDelete) {
          delete filterData[row]
          boolDelete = false
        }
      }
      if (exceptionArr !== undefined) {
        var count = 0
        for (var g in filterData) {
          delete Object.assign(filterData, { [count]: filterData[g] })[g]
          count++
        }
      }
      return filterData
    },
    myRegExp (mas, string) {
      if (mas === undefined) {
        return true
      }
      var str = '('
      for (var i in mas) {
        if (parseInt(i) + 1 !== mas.length) {
          str += mas[i] + '|'
        } else str += mas[i]
      }
      str += ')'
      // console.log(str)
      var myRe = new RegExp(str, 'g')
      if (myRe.test(string)) return false
      else return true
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .fb-col{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
  }
  .fb-row{
    background: #fbfbfb;
    width: 85%;
    cursor: pointer;
    padding: 5px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
  }
  .fb-row:nth-child(2n){
    background: #f8eee7;
  }
  .fb-row:hover{
    text-decoration: underline;
    background: #008dff;
    color: #ffffff;;
    transition: all 1s ease-in-out;
    box-shadow: 0 0 5px 1px black;
  }
  .fb-row>*{
    margin-right: 5px;
  }
  .fb-row>*:last-child{
    margin-right: 15px;
  }
  .fb-row .number{
    margin: 0;
    margin-right: 15px;
    margin-left: 10px;
  }
</style>
