<template>
  <div class="WeddingList">
    <div style="display: block; height: 70px;"></div>
    <div class="btn-group">
      <input @click="on" type="button" value="Включить">
      <input @click="off" type="button" value="Выключить">
    </div>
    <InviteList :data="content" :filters="filters"/>
  </div>
</template>
<script>
import axios from 'axios'
import InviteList from '../components/InviteList.vue'

export default {
  name: 'WeddingList',
  components: {
    InviteList
  },
  created () {
    this.getData()
  },
  data () {
    return {
      content: {},
      filters: true
    }
  },
  methods: {
    on () {
      this.filters = true
    },
    off () {
      this.filters = false
    },
    getData () {
      axios('https://spreadsheets.google.com/feeds/list/1tFcDbWFRTYtHbs4opQx-qz18hxZS6myCFko6RUnZrPk/od6/public/values?alt=json', {
        method: 'GET'
      }).then((response) => {
        this.content = this.getAndToObj(response)
      })
    },
    getAndToObj (response) {
      var Obj = response.data.feed.entry
      var ObjNameStrok = this.parsToNameStrok(response)

      var row = 0
      var name = ''

      var GObj = {}
      var mas = []
      for (var key in ObjNameStrok) {
        mas = []
        for (var st in ObjNameStrok[key]) {
          row = key
          name = ObjNameStrok[key][st]
          mas.push(Obj[row][name].$t)
        }
        GObj[key] = mas
      }
      return GObj
    },
    parsToNameStrok (data) {
      var arrData = data.data.feed.entry
      var myRe = new RegExp('gsx\\$', 'g')
      var masGsx = []
      var ItogObg = {}
      var temp = []
      for (var i = 0; i < arrData.length; i++) {
        temp = Object.keys(arrData[i])
        masGsx = []
        for (var j = 0; j < temp.length; j++) {
          if (myRe.test(temp[j])) {
            myRe.lastIndex = 0
            masGsx.push(temp[j])
          }
        }
        ItogObg[i] = masGsx
      }
      return ItogObg
    }
  }
}
</script>
<style>
  input[type='button']{
    font-size: 20px;
    margin: 30px;
    padding: 5px 15px;
    border-radius: 5px;
    background-color: #01a455;
    box-shadow: 0 2px 0 0 #057b42;
    border: none;
    color: white;
    cursor: pointer;
  }
  input[type='button']:hover{
    background-color: #15c26e;
    transition: all 300ms ease-in-out;
  }
  .btn-group input[type='button']{
     margin: 30px 0px;
  }
  .btn-group input[type='button']:first-child{
    border-radius: 5px 0 0 5px;
    border-right: 1px solid black;
  }
  .btn-group input[type='button']:last-child{
    border-radius: 0 5px 5px 0;
  }
  input[type='button'].active{
    background-color: #15c26e;
  }
</style>
